body {
  background-color: #c5c4c4;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Fira Sans", sans-serif;
}
.heading {
  background-color: #9e9d9d;
  padding: 5px;
  border-radius: 5px;
}

